export function updateUserRequest(token, id, body) {
  return {
    type: '@users/UPDATE_USER_REQUEST',
    payload: { token, id, body },
  };
}

export function updateUserSuccess(response) {
  return {
    type: '@users/UPDATE_USER_SUCCESS',
    payload: { response },
  };
}

export function updateUserFailure(error) {
  return {
    type: '@users/UPDATE_USER_FAILURE',
    payload: { error },
  };
}

export function updateUserClear() {
  return {
    type: '@users/UPDATE_USER_CLEAR',
  };
}
