/* eslint-disable radix */
/* eslint-disable prefer-template */
/* eslint-disable no-confusing-arrow */
/* eslint-disable prefer-template */
import React, { useState, useMemo, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { PermissibleRender } from '@brainhubeu/react-permissible';
import Table from '@material-ui/core/Table';
import TableContainer from '@material-ui/core/TableContainer';
import { Autocomplete, Pagination } from '@material-ui/lab';
import Paper from '@material-ui/core/Paper';
import moment from 'moment';
import { Grid, TextField } from '@material-ui/core';
import {
  StyledTableCell,
  StyledTableRow,
  StyledTableHead,
  StyledTableBody,
  useStyles,
  IconShowTransaction,
  Button,
  Title,
  Text,
  HeaderPage,
  TableFooter,
  SearchField,
  ReportHeaderField,
  Flex,
  Body,
  WarningMessage,
  // eslint-disable-next-line import/named
} from './styles';
import LoadingComponent from '../../components/loading';
import {
  getTransactions,
  getTransactionsCleaner,
} from '../../store/modules/getTransactions/actions';
import { HeaderInputs } from './headerInputs';
import GetTransactionModal from '../../components/transactions/getTransaction/getTransaction';
import { getMerchants } from '../../store/modules/merchantGet/actions';
import { showMerchant } from '../../store/modules/merchantShow/actions';

const INITIAL_QUERY = {
  sort_order: 'DESC',
  sort_field: 'created_at',
  limit: 25,
  page: 1,
  serial_pos: null,
  merchant_id: null,
};
const Master = () => {
  const classes = useStyles();
  const { t, i18n } = useTranslation();
  const [query, setQuery] = useState(INITIAL_QUERY);

  const token = useSelector((state) => state.auth.token);
  const user = useSelector((state) => state.user.data);
  const test = useSelector((state) => state);
  const transactions = useSelector((state) => state.getTransactions);
  const merchants = useSelector((state) => state.getMerchants);
  const merchantSelected = useSelector((state) => state.showMerchant);
  const [getTransactionIsOpen, setGetTransactionIsOpen] = useState(false);
  const [currentId, setCurrentId] = useState({});
  console.log(
    transactions.data,
    transactions.data?.transactions,
    transactions.data?.transactions.length,
    transactions.data?.transactions.total > 0,
    transactions.loading,
    transactions.error
  );

  const dispatch = useDispatch();

  const handleChangePage = async (e, newPage) => {
    setQuery((state) => ({
      ...state,
      page: newPage,
    }));
  };

  const handleGetTransactionItem = (id) => {
    setCurrentId(id);
    setGetTransactionIsOpen(!getTransactionIsOpen);
  };

  useMemo(() => {
    query.merchant_id && dispatch(getTransactions(token, query));
  }, [query.page]);

  const closeModalGetTransactionOnSuccess = () => {
    setGetTransactionIsOpen(false);
  };
  useMemo(() => {
    query.merchant_id && dispatch(getTransactions(token, query));
    query.merchant_id && dispatch(showMerchant(token, query.merchant_id));
  }, [query.merchant_id]);

  useMemo(() => {
    console.log(user.type, query.merchant_id, test);
    user.type === 1
      ? dispatch(getMerchants(token))
      : dispatch(getTransactions(token, query));
  }, []);

  return (
    <Body>
      <HeaderPage>
        <SearchField>
          <Title style={{ width: '30%', marginRight: '50px' }}>
            {t('routes.transactions')}
          </Title>
          <PermissibleRender
            userPermissions={user.type === 1}
            requiredPermissions={user.type === 1}
            renderOtherwide={null}
          >
            <Autocomplete
              id="autocomplete-clients"
              options={merchants?.data?.items}
              disabled={merchants.loading}
              getOptionLabel={(items) => items.name}
              style={{ width: '60%' }}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label={t('table.client')}
                  variant="outlined"
                />
              )}
              onChange={(e, target) => {
                target
                  ? setQuery((state) => ({
                      ...state,
                      merchant_id: target.id,
                    }))
                  : dispatch(getTransactionsCleaner());

                !target &&
                  setQuery((state) => ({
                    ...state,
                    merchant_id: null,
                  }));
              }}
            />
          </PermissibleRender>
        </SearchField>
        {merchantSelected.data && (
          <ReportHeaderField container style={{ padding: '10px' }}>
            <Flex style={{ flexDirection: 'column', justifyContent: 'center' }}>
              <Text color="#5DDE5D" style={{ fontWeight: 'bold' }}>
                {t('table.balance')}
              </Text>
              <Text color="#5DDE5D">
                {new Intl.NumberFormat('pt-BR', {
                  style: 'currency',
                  currency: 'BRL',
                }).format(merchantSelected.data.client_account.balance / 100)}
              </Text>
            </Flex>
            <Flex style={{ flexDirection: 'column', justifyContent: 'center' }}>
              <Text color="blue">{t('table.merchant_withdraw')}</Text>
              <Text color="blue">
                {' '}
                {new Intl.NumberFormat('pt-BR', {
                  style: 'currency',
                  currency: 'BRL',
                }).format(
                  merchantSelected.data.client_account.merchant_withdraw / 100
                )}
              </Text>
            </Flex>
            <Flex style={{ flexDirection: 'column', justifyContent: 'center' }}>
              <Text color="#999">{t('table.total_sales')}</Text>
              <Text color="#999">
                {' '}
                {new Intl.NumberFormat('pt-BR', {
                  style: 'currency',
                  currency: 'BRL',
                }).format(
                  (parseInt(merchantSelected.data.client_account.pix) +
                    parseInt(merchantSelected.data.client_account.bankslip) +
                    parseInt(merchantSelected.data.client_account.card) +
                    parseInt(merchantSelected.data.client_account.posCard)) /
                    100
                )}
              </Text>
            </Flex>
          </ReportHeaderField>
        )}
      </HeaderPage>
      {transactions.data &&
      transactions.data.transactions &&
      transactions.data.transactions.length &&
      transactions.data.total > 0 &&
      !transactions.loading &&
      !transactions.error ? (
        <>
          <HeaderInputs
            transactions={transactions}
            getTransactions={getTransactions}
            dispatch={dispatch}
            setQuery={setQuery}
            query={query}
            token={token}
          />
          <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="customized table">
              <StyledTableHead>
                <StyledTableRow>
                  <StyledTableCell>ID</StyledTableCell>
                  <StyledTableCell>
                    {t('table.transaction_type')}
                  </StyledTableCell>
                  <StyledTableCell>{t('table.value')}</StyledTableCell>
                  <StyledTableCell>{t('table.created_at')}</StyledTableCell>
                  <StyledTableCell>{t('table.delivered_at')}</StyledTableCell>
                  <StyledTableCell>{t('table.payer_name')}</StyledTableCell>
                  <StyledTableCell>{t('table.status')}</StyledTableCell>
                  <StyledTableCell>{t('table.actions')}</StyledTableCell>
                </StyledTableRow>
              </StyledTableHead>
              <StyledTableBody>
                {transactions?.data?.transactions.map((value) => (
                  <StyledTableRow>
                    <StyledTableCell>{value.id}</StyledTableCell>
                    <StyledTableCell>{value.transaction_type}</StyledTableCell>
                    <StyledTableCell>
                      {new Intl.NumberFormat('pt-BR', {
                        style: 'currency',
                        currency: 'BRL',
                      }).format(value.value / 100)}
                    </StyledTableCell>
                    <StyledTableCell>
                      {value.created_at
                        ? moment
                            .utc(value.created_at)
                            .utcOffset('GMT-03:00')
                            .format('DD-MM-YYYY HH:mm')
                        : '-'}
                    </StyledTableCell>
                    <StyledTableCell>
                      {value.delivered_at
                        ? moment
                            .utc(value.delivered_at)
                            .utcOffset('GMT-03:00')
                            .format('DD-MM-YYYY HH:mm')
                        : '-'}
                    </StyledTableCell>
                    <StyledTableCell>{value?.payer?.name}</StyledTableCell>
                    <StyledTableCell>
                      {value.status
                        ? t(
                            'table.' +
                              value.status.toLowerCase().replaceAll(' ', '_')
                          )
                        : '-'}
                    </StyledTableCell>
                    <StyledTableCell>
                      <Grid container justify="center">
                        <Button
                          background="#626edf"
                          onClick={(e) => handleGetTransactionItem(value.id)}
                        >
                          <IconShowTransaction />
                        </Button>
                      </Grid>
                    </StyledTableCell>
                  </StyledTableRow>
                ))}
              </StyledTableBody>
            </Table>
            {getTransactionIsOpen && (
              <>
                <GetTransactionModal
                  open={getTransactionIsOpen}
                  closeModal={closeModalGetTransactionOnSuccess}
                  id={currentId}
                />
              </>
            )}
          </TableContainer>
        </>
      ) : null}
      <TableFooter>
        {transactions?.data?.total > 25 && (
          <Pagination
            count={Math.ceil(transactions?.data?.total / 25)}
            page={query.page}
            color="primary"
            onChange={handleChangePage}
          />
        )}
      </TableFooter>
      {transactions.loading && <LoadingComponent />}
      {transactions.error && (
        <WarningMessage>{transactions.error.message}</WarningMessage>
      )}
      {transactions.data && transactions.data.total === 0 && (
        <WarningMessage>Nothing to show</WarningMessage>
      )}
      {!transactions.error &&
        !transactions.loading &&
        !transactions.data &&
        !transactions.success && (
          <PermissibleRender
            userPermissions={user.type === 1}
            requiredPermissions={user.type === 1}
            renderOtherwide={null}
          >
            <WarningMessage>Select a merchant!</WarningMessage>
          </PermissibleRender>
        )}
    </Body>
  );
};

export default Master;
