export function createMasterRequest(token, body) {
  return {
    type: '@masters/CREATE_MASTER_REQUEST',
    payload: { token, body },
  };
}

export function createMasterSuccess(response) {
  return {
    type: '@masters/CREATE_MASTER_SUCCESS',
    payload: { response },
  };
}

export function createMasterClear() {
  return {
    type: '@masters/CREATE_MASTER_CLEAR',
  };
}

export function createMasterFailure(error) {
  return {
    type: '@masters/CREATE_MASTER_FAILURE',
    payload: { error },
  };
}
