import { all, put, takeLatest } from '@redux-saga/core/effects';
import api from '../../../services/api';

import { updateMasterSuccess, updateMasterFailure } from './actions';

function* updateMaster({ payload }) {
  const { token, body, id } = payload;
  try {
    const response = yield api.put(`dashboard/master/${id}`, body, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    console.log('UPDATE MASTER SAGAS RESPONSE', response.data);
    yield put(updateMasterSuccess(response.data));
  } catch (error) {
    yield put(updateMasterFailure(error));
  }
}

export default all([
  takeLatest('@masters/UPDATE_MASTER_REQUEST', updateMaster),
]);
