/* eslint-disable no-confusing-arrow */
import React, { useState, useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Table from '@material-ui/core/Table';
import TableContainer from '@material-ui/core/TableContainer';
import { useTranslation } from 'react-i18next';
import Paper from '@material-ui/core/Paper';
import { Autocomplete, Pagination } from '@material-ui/lab';
import { TextField, Switch, Grid } from '@material-ui/core';
import { IoMdPersonAdd } from 'react-icons/io';
import Moment from 'react-moment';
import moment from 'moment';
import Title from '../../components/title';
import {
  StyledTableCell,
  StyledTableRow,
  StyledTableHead,
  StyledTableBody,
  useStyles,
  Header,
  AddButton,
  Button,
  IconUserEdit,
  IconUserDelete,
  SorteLabel,
  WarningMessage,
  TableFooter,
} from './styles';
import LoadingComponent from '../../components/loading';
import { CreateUserModal } from './CreateUserModal';
import { UpdateUserModal } from './UpdateUserModal';
import { getUsers } from '../../store/modules/getUsers/actions';
import { updateUserRequest } from '../../store/modules/userUpdate/actions';
import { DeleteUserModal } from './DeleteUserModal';

const INITIAL_QUERY = {
  sort_order: 'DESC',
  sort_field: 'id',
  limit: 25,
  page: 1,
};
const User = () => {
  const classes = useStyles();
  const { t, i18n } = useTranslation();
  const [openCreateUser, setOpenCreateUser] = useState(false);
  const [openUpdateUser, setOpenUpdateUser] = useState(false);
  const [openDeleteUser, setOpenDeleteUser] = useState(false);
  const [query, setQuery] = useState(INITIAL_QUERY);

  const token = useSelector((state) => state.auth.token);
  const users = useSelector((state) => state.getUsers);
  const userCreated = useSelector((state) => state.createUser);
  const userUpdated = useSelector((state) => state.updateUser);
  const userDeleted = useSelector((state) => state.deleteUser);
  const [currentUser, setCurrentUser] = useState({});

  const dispatch = useDispatch();

  function descendingComparator(a, b, orderBy) {
    if (b[orderBy] < a[orderBy]) {
      return -1;
    }
    if (b[orderBy] > a[orderBy]) {
      return 1;
    }
    return 0;
  }

  function getComparator(order, orderBy) {
    return order === 'desc'
      ? (a, b) => descendingComparator(a, b, orderBy)
      : (a, b) => -descendingComparator(a, b, orderBy);
  }

  function stableSort(array, comparator) {
    const stabilizedThis = array.map((el, index) => [el, index]);
    stabilizedThis.sort((a, b) => {
      const order = comparator(a[0], b[0]);
      if (order !== 0) return order;
      return a[1] - b[1];
    });
    return stabilizedThis.map((el) => el[0]);
  }

  const handleSortOrderColumn = (column) => {
    if (column === query.sort_field) {
      query.sort_order === 'desc'
        ? setQuery((state) => ({ ...state, sort_order: 'asc' }))
        : setQuery((state) => ({ ...state, sort_order: 'desc' }));
    }
    setQuery((state) => ({ ...state, sort_field: column }));
  };
  const handleUpdateStatus = (id, value) => {
    dispatch(updateUserRequest(token, id, { status: value }));
  };

  const handleChangePage = (event, newPage) => {
    setQuery((state) => ({ ...state, page: newPage }));
  };

  const handleCloseCreateUserModal = () => {
    setOpenCreateUser(false);
  };

  const handleUserUpdateModal = (obj) => {
    setCurrentUser(obj);
    setOpenUpdateUser(!openUpdateUser);
  };

  const handleCloseUpdateUserModal = () => {
    setOpenUpdateUser(false);
  };

  const handleUserDeleteModal = (obj) => {
    setCurrentUser(obj);
    setOpenDeleteUser(!openDeleteUser);
  };

  const closeUserDeleteModal = () => {
    setOpenDeleteUser(false);
  };

  useMemo(() => {
    users.success &&
      dispatch(
        getUsers(token, {
          sort_order: query.sort_order === 'dsc' ? 'DESC' : 'ASC',
          sort_field: query.sort_field,
          limit: query.limit,
          page: query.page,
        })
      );
  }, [query.page]);

  useMemo(() => {
    (userCreated.success || userUpdated.success || userDeleted.success) &&
      dispatch(getUsers(token, query));
  }, [userCreated.success, userUpdated.success, userDeleted.success]);

  useMemo(() => {
    dispatch(getUsers(token, query));
  }, []);

  return (
    <>
      <Title text={t('routes.merchant')} />
      {users.data && users.data.items && users.data.items.length && (
        <>
          <Header>
            <Grid
              container
              direction="row"
              justify="flex-end"
              align="center"
              style={{
                width: '100%',
              }}
            >
              <AddButton onClick={() => setOpenCreateUser(true)}>
                <IoMdPersonAdd style={{ fontSize: '20px' }} />
              </AddButton>
            </Grid>
          </Header>

          <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="customized table">
              <StyledTableHead>
                <StyledTableRow>
                  <StyledTableCell
                    sortDirection={
                      query.sort_field === 'id' ? query.sort_order : false
                    }
                  >
                    <SorteLabel
                      active={query.sort_field === 'id'}
                      direction={query.sort_order.toLowerCase()}
                      onClick={() => handleSortOrderColumn('id')}
                    >
                      ID
                    </SorteLabel>
                  </StyledTableCell>
                  <StyledTableCell
                    sortDirection={
                      query.sort_field === 'name' ? query.sort_order : false
                    }
                  >
                    <SorteLabel
                      active={query.sort_field === 'name'}
                      direction={query.sort_order.toLowerCase()}
                      onClick={() => handleSortOrderColumn('name')}
                    >
                      {t('table.name')}
                    </SorteLabel>
                  </StyledTableCell>
                  <StyledTableCell
                    sortDirection={
                      query.sort_field === 'username' ? query.sort_order : false
                    }
                  >
                    <SorteLabel
                      active={query.sort_field === 'username'}
                      direction={query.sort_order.toLowerCase()}
                      onClick={() => handleSortOrderColumn('username')}
                    >
                      {t('table.username')}
                    </SorteLabel>
                  </StyledTableCell>
                  <StyledTableCell
                    sortDirection={
                      query.sort_field === 'status' ? query.sort_order : false
                    }
                  >
                    <SorteLabel
                      active={query.sort_field === 'status'}
                      direction={query.sort_order.toLowerCase()}
                      onClick={() => handleSortOrderColumn('status')}
                    >
                      {t('table.status')}
                    </SorteLabel>
                  </StyledTableCell>
                  <StyledTableCell
                    sortDirection={
                      query.sort_field === 'last_signin_date'
                        ? query.sort_order
                        : false
                    }
                  >
                    <SorteLabel
                      active={query.sort_field === 'last_signin_date'}
                      direction={query.sort_order.toLowerCase()}
                      onClick={() => handleSortOrderColumn('last_signin_date')}
                    >
                      {t('table.last_signin_date')}
                    </SorteLabel>
                  </StyledTableCell>
                  <StyledTableCell
                    sortDirection={
                      query.sort_field === 'actions' ? query.sort_order : false
                    }
                  >
                    {t('table.actions')}
                  </StyledTableCell>
                </StyledTableRow>
              </StyledTableHead>
              <StyledTableBody>
                {stableSort(
                  users.data.items,
                  getComparator(query.sort_order, query.sort_field)
                ).map((value) => (
                  <StyledTableRow>
                    <StyledTableCell>{value.id}</StyledTableCell>
                    <StyledTableCell>{value.name}</StyledTableCell>
                    <StyledTableCell>{value.username}</StyledTableCell>
                    <StyledTableCell>
                      <Switch
                        color="primary"
                        disabled={userUpdated.loading}
                        checked={value.status}
                        onClick={(e, target) =>
                          handleUpdateStatus(value.id, !value.status)
                        }
                      />
                    </StyledTableCell>
                    <StyledTableCell>
                      {value.last_signin_date
                        ? moment
                            .utc(value.last_signin_date)
                            .format('DD-MM-YYYY HH:mm')
                        : '-'}
                    </StyledTableCell>
                    <StyledTableCell>
                      <Grid container justify="center" direction="row">
                        <Button
                          background="#626edf"
                          variant="update"
                          onClick={(e) => handleUserUpdateModal(value)}
                        >
                          <IconUserEdit />
                        </Button>
                        <Button
                          background="#d01a24"
                          onClick={(e) => handleUserDeleteModal(value)}
                        >
                          <IconUserDelete />
                        </Button>
                      </Grid>
                    </StyledTableCell>
                  </StyledTableRow>
                ))}
              </StyledTableBody>
            </Table>
          </TableContainer>
          <TableFooter>
            {users?.data?.total > 25 && (
              <Pagination
                count={Math.ceil(users?.data?.total / 25)}
                page={query.page}
                color="primary"
                onChange={handleChangePage}
              />
            )}
          </TableFooter>
        </>
      )}
      {users.loading && <LoadingComponent />}
      {users.error && <WarningMessage>{users.error.message}</WarningMessage>}
      {openUpdateUser && (
        <>
          <UpdateUserModal
            currentUser={currentUser}
            open={openUpdateUser}
            closeModal={handleCloseUpdateUserModal}
          />
        </>
      )}
      {openDeleteUser && (
        <>
          <DeleteUserModal
            currentUser={currentUser}
            open={openDeleteUser}
            closeModal={closeUserDeleteModal}
          />
        </>
      )}
      <CreateUserModal
        openModal={openCreateUser}
        closeModal={handleCloseCreateUserModal}
      />
    </>
  );
};

export default User;
