import { all, takeLatest, call, put } from 'redux-saga/effects';
import api from '../../../services/api';

import { getTransactionsSuccess, getTransactionsFailure } from './actions';

function* getTransactions({ payload }) {
  const { token, params } = payload;

  try {
    const response = yield api.get('report/transactions', {
      params,
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    yield put(getTransactionsSuccess(response.data));
  } catch (error) {
    yield put(getTransactionsFailure(error));
  }
}

export default all([
  takeLatest('@transactions/GET_TRANSACTION_REQUEST', getTransactions),
]);
