export function getMerchants(token) {
  return {
    type: '@merchants/GET_MERCHANTS_REQUEST',
    payload: { token },
  };
}

export function getMerchantsRequests(users) {
  return {
    type: '@merchants/GET_MERCHANTS_SUCCESS',
    payload: { users },
  };
}

export function getMerchantsFailure(error) {
  return {
    type: '@merchants/GET_MERCHANTS_FAILURE',
    payload: { error },
  };
}
