import produce from 'immer';

const INITIAL_STATE = {
  success: false,
  data: null,
  loading: false,
  error: null,
};

export default function getUsers(state = INITIAL_STATE, action) {
  const { type, payload } = action;
  return produce(state, (draft) => {
    switch (type) {
      case '@users/GET_USERS_REQUEST': {
        draft.loading = true;
        draft.data = null;
        draft.success = false;
        break;
      }

      case '@users/GET_USERS_SUCCESS': {
        draft.loading = false;
        draft.data = payload.users;
        draft.success = true;
        break;
      }

      case '@users/GET_USERS_FAILURE': {
        draft.loading = false;
        draft.error = payload.error;
        draft.success = false;
        break;
      }

      case '@auth/SIGN_OUT': {
        draft.loading = false;
        draft.data = null;
        draft.success = false;
        draft.error = null;
        break;
      }
      default:
    }
  });
}
