import { Grid } from '@material-ui/core';
import React, { useMemo, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import ButtonComponent from '../../components/button';
import InputComponent from '../../components/input';
import LoadingComponent from '../../components/loading';
import ModalComponent from '../../components/modal';
import {
  createMasterClear,
  createMasterRequest,
} from '../../store/modules/CreateMaster/actions';

const INITIAL_STATE = {
  name: null,
  username: null,
  password: null,
};
export const CreateUserModal = ({ openModal, closeModal }) => {
  const [newUser, setNewUser] = useState(INITIAL_STATE);
  const [confirmPassword, setConfirmPassowrd] = useState('');
  const [wrongPassword, setWrongPassword] = useState(false);
  const { t, i18n } = useTranslation();
  const token = useSelector((state) => state.auth.token);
  const userCreated = useSelector((state) => state.createMaster);
  const dispatch = useDispatch();

  const handleSubmit = (ev) => {
    ev.preventDefault();

    confirmPassword !== newUser.password
      ? setWrongPassword(true)
      : dispatch(createMasterRequest(token, newUser));
  };

  useMemo(() => {
    userCreated.success && closeModal();
    userCreated.success && setWrongPassword(false);
    userCreated.success && dispatch(createMasterClear());
  }, [userCreated]);
  return (
    <ModalComponent
      modalState={openModal}
      onClose={closeModal}
      title={t('modal.create_user')}
    >
      {!userCreated.loading ? (
        <form onSubmit={handleSubmit}>
          <Grid container justify="start" style={{ marginTop: '10px' }}>
            <InputComponent
              required
              label={t('modal.name')}
              onChange={(e) => {
                setNewUser((state) => ({ ...state, name: e.target.value }));
              }}
            />
          </Grid>
          <Grid container justify="start" style={{ marginTop: '10px' }}>
            <InputComponent
              required
              label={t('modal.username')}
              onChange={(e) => {
                setNewUser((state) => ({ ...state, username: e.target.value }));
              }}
            />
          </Grid>
          <Grid container justify="start" style={{ marginTop: '10px' }}>
            <InputComponent
              required
              isInvalid={wrongPassword}
              label={t('modal.password')}
              type="password"
              onChange={(e) => {
                setNewUser((state) => ({ ...state, password: e.target.value }));
              }}
            />
          </Grid>
          <Grid container justify="start" style={{ marginTop: '10px' }}>
            <InputComponent
              required
              isInvalid={wrongPassword}
              label={t('modal.type_password_confirm')}
              type="password"
              onChange={(e) => {
                setConfirmPassowrd(e.target.value);
              }}
            />
          </Grid>
          <Grid container justify="center">
            {wrongPassword && (
              <p style={{ color: 'red' }}>{t('modal.password_not_match')}</p>
            )}
          </Grid>
          <Grid
            container
            justify="space-between"
            style={{ marginTop: '20px', marginBottom: '10px' }}
          >
            <ButtonComponent
              width="40%"
              variant="danger"
              label={t('modal.return')}
              onClick={closeModal}
            />
            <ButtonComponent
              width="40%"
              variant="primary"
              label={t('modal.create')}
              type="submit"
            />
          </Grid>
        </form>
      ) : (
        <LoadingComponent />
      )}
    </ModalComponent>
  );
};
