import { all, takeLatest, call, put } from 'redux-saga/effects';
import api from '../../../services/api';

import { showTransactionSuccess, showTransactionFailure } from './actions';

function* showTransaction({ payload }) {
  const { token, transactionId } = payload;

  try {
    const response = yield api.get(`report/transactions/${transactionId}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });

    yield put(showTransactionSuccess(response.data));
  } catch (error) {
    yield put(showTransactionFailure(error));
  }
}

export default all([
  takeLatest('@transaction/SHOW_TRANSACTION_REQUEST', showTransaction),
]);
