export function deleteMaster(token, id) {
  console.log('ACTIONS DELETE MASTER');
  return {
    type: '@masters/DELETE_MASTER_REQUEST',
    payload: { token, id },
  };
}

export function deleteMasterSuccess() {
  return {
    type: '@masters/DELETE_MASTER_SUCCESS',
  };
}

export function deleteMasterFailure(error) {
  return {
    type: '@masters/DELETE_MASTER_FAILURE',
    payload: { error },
  };
}

export function deleteMasterClear() {
  return {
    type: '@masters/DELETE_MASTER_CLEAR',
  };
}
