export function showTransactionRequest(token, transactionId) {
  return {
    type: '@transaction/SHOW_TRANSACTION_REQUEST',
    payload: { token, transactionId },
  };
}

export function showTransactionSuccess(transaction) {
  return {
    type: '@transaction/SHOW_TRANSACTION_SUCCESS',
    payload: { transaction },
  };
}

export function showTransactionFailure(error) {
  return {
    type: '@transaction/SHOW_TRANSACTION_FAILURE',
    payload: { error },
  };
}
