import { all, put, takeLatest } from '@redux-saga/core/effects';
import api from '../../../services/api';

import { deleteMasterSuccess, deleteMasterFailure } from './actions';

function* deleteMaster({ payload }) {
  const { token, id } = payload;
  console.log('REDUCER PASSOU DO TRY');
  try {
    yield api.delete(`dashboard/master/${id}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    console.log('REDUCER PASSOU DO TRY');
    yield put(deleteMasterSuccess());
  } catch (error) {
    yield put(deleteMasterFailure(error));
  }
}

export default all([
  takeLatest('@masters/DELETE_MASTER_REQUEST', deleteMaster),
]);
