import produce from 'immer';

const INITIAL_STATE = {
  success: false,
  loading: false,
  error: null,
};

export default function deleteUser(state = INITIAL_STATE, action) {
  const { type, payload } = action;
  return produce(state, (draft) => {
    switch (type) {
      case '@users/DELETE_USER_REQUEST': {
        draft.success = false;
        draft.loading = true;
        draft.error = null;
        break;
      }

      case '@users/DELETE_USER_SUCCESS': {
        draft.success = true;
        draft.loading = false;
        draft.error = null;
        break;
      }

      case '@users/DELETE_USER_FAILURE': {
        draft.success = false;
        draft.loading = false;
        draft.error = payload.error;
        break;
      }

      case '@users/DELETE_USER_CLEAR': {
        draft.success = false;
        draft.loading = false;
        draft.error = null;
        break;
      }
      default:
    }
  });
}
