import { Grid, TextField } from '@material-ui/core';
import React, { useMemo, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { Autocomplete } from '@material-ui/lab';
import ButtonComponent from '../../components/button';
import InputComponent from '../../components/input';
import LoadingComponent from '../../components/loading';
import ModalComponent from '../../components/modal';
import {
  createMasterClear,
  createMasterRequest,
} from '../../store/modules/CreateMaster/actions';
import { getMerchants } from '../../store/modules/merchantGet/actions';
import {
  createUsersCleaner,
  createUsersRequest,
} from '../../store/modules/CreateUser/actions';

const INITIAL_STATE = {
  name: null,
  username: null,
  password: null,
  merchant_id: null,
};
export const CreateUserModal = ({ openModal, closeModal }) => {
  const [newUser, setNewUser] = useState(INITIAL_STATE);
  const [confirmPassword, setConfirmPassowrd] = useState('');
  const [wrongPassword, setWrongPassword] = useState(false);
  const [notClientId, setNotClientId] = useState(false);
  const { t, i18n } = useTranslation();
  const token = useSelector((state) => state.auth.token);

  const merchants = useSelector((state) => state.getMerchants);
  const userCreated = useSelector((state) => state.createUser);
  const dispatch = useDispatch();

  const handleSubmit = (ev) => {
    ev.preventDefault();
    !newUser.client_id ? setNotClientId(true) : setNotClientId(false);
    confirmPassword !== newUser.password
      ? setWrongPassword(true)
      : setWrongPassword(false);
    console.log(notClientId, wrongPassword);
  };

  const resetStates = () => {
    setNotClientId(false);
    setWrongPassword(false);
    setNewUser(INITIAL_STATE);
    dispatch(createUsersCleaner());
  };

  useMemo(() => {
    console.log(notClientId, wrongPassword);
    !notClientId &&
      !wrongPassword &&
      dispatch(createUsersRequest(token, newUser));
  }, [wrongPassword, notClientId]);

  useMemo(() => {
    userCreated.success && closeModal();
    userCreated.success && resetStates();
  }, [userCreated]);

  useMemo(() => {
    dispatch(getMerchants(token));
  }, []);
  return (
    <ModalComponent
      modalState={openModal}
      onClose={closeModal}
      title={t('modal.create_user')}
    >
      {!userCreated.loading ? (
        <form onSubmit={handleSubmit}>
          <Grid container justify="start" style={{ marginTop: '10px' }}>
            <Autocomplete
              id="autocomplete-clients"
              required
              options={merchants?.data?.items}
              disabled={merchants.loading}
              getOptionLabel={(items) => items.name}
              style={{ width: '100%' }}
              renderInput={(params) => (
                <TextField {...params} label="Client" variant="outlined" />
              )}
              onChange={(e, target) => {
                setNewUser((state) => ({
                  ...state,
                  client_id: target ? target.id : null,
                }));
              }}
            />
          </Grid>
          <Grid container justify="start" style={{ marginTop: '10px' }}>
            <InputComponent
              required
              label={t('modal.name')}
              onChange={(e) => {
                setNewUser((state) => ({ ...state, name: e.target.value }));
              }}
            />
          </Grid>
          <Grid container justify="start" style={{ marginTop: '10px' }}>
            <InputComponent
              required
              label={t('modal.username')}
              onChange={(e) => {
                setNewUser((state) => ({ ...state, username: e.target.value }));
              }}
            />
          </Grid>
          <Grid container justify="start" style={{ marginTop: '10px' }}>
            <InputComponent
              required
              isInvalid={wrongPassword}
              label={t('modal.password')}
              type="password"
              onChange={(e) => {
                setNewUser((state) => ({ ...state, password: e.target.value }));
              }}
            />
          </Grid>
          <Grid container justify="start" style={{ marginTop: '10px' }}>
            <InputComponent
              required
              isInvalid={wrongPassword}
              label={t('modal.type_password_confirm')}
              type="password"
              onChange={(e) => {
                setConfirmPassowrd(e.target.value);
              }}
            />
          </Grid>
          <Grid container justify="center">
            {wrongPassword && (
              <p style={{ color: 'red' }}>{t('modal.password_not_match')}</p>
            )}
          </Grid>
          <Grid container justify="center">
            {notClientId && (
              <p style={{ color: 'red' }}>{t('modal.select_client')}</p>
            )}
          </Grid>
          <Grid
            container
            justify="space-between"
            style={{ marginTop: '20px', marginBottom: '10px' }}
          >
            <ButtonComponent
              variant="danger"
              width="45%"
              label={t('modal.return')}
              onClick={closeModal}
            />
            <ButtonComponent
              variant="primary"
              width="45%"
              label={t('modal.create')}
              type="submit"
            />
          </Grid>
        </form>
      ) : (
        <LoadingComponent />
      )}
    </ModalComponent>
  );
};
