export function deleteUser(token, id) {
  console.log('ACTIONS DELETE USER');
  return {
    type: '@users/DELETE_USER_REQUEST',
    payload: { token, id },
  };
}

export function deleteUserSuccess() {
  return {
    type: '@users/DELETE_USER_SUCCESS',
  };
}

export function deleteUserFailure(error) {
  return {
    type: '@users/DELETE_USER_FAILURE',
    payload: { error },
  };
}

export function deleteUserClear() {
  return {
    type: '@users/DELETE_USER_CLEAR',
  };
}
