/* eslint-disable no-nested-ternary */
import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { format, parseISO } from 'date-fns';
import { Collapse, Grid, Fade, Slide, withWidth } from '@material-ui/core';
import Image from 'material-ui-image';
import { PermissibleRender } from '@brainhubeu/react-permissible';
import { useTranslation } from 'react-i18next';
import BrazilLogo from '../../assets/images/logo_bra.svg';
import SpainLogo from '../../assets/images/logo_esp.svg';
import EuaLogo from '../../assets/images/logo_eua.svg';
import { signOut } from '../../store/modules/auth/actions';
import imagem from '../../assets/images/Logo-pay-brokers-colorido (1).png';
import ModalChangePassword from './changePassword';
import {
  Header,
  MobileMenu,
  Menu,
  Content,
  OptionMenu,
  Text,
  IconUsers,
  IconReport,
  IconMaster,
  SubOptionMenu,
  SubText,
  IconSingleUser,
  IconGraphic,
  IconBiExit,
  IconBiLock,
  IconHamburgerMenu,
  UserContainer,
  UserInfo,
  UserSettings,
  Settings,
  SettingsName,
  LastLogin,
  LastLoginText,
  SettingsOptions,
  CountryFlag,
  IconBackMenu,
  IconBiFace,
} from './styles';
import history from '../../services/history';
import { device } from '../../devices';

const NavigationComponent = ({ children }) => {
  const [option1, setOption1] = useState(false);
  const [option2, setOption2] = useState(false);
  const [openMobile, setOpenMobile] = useState(false);
  const [openSettings, setOpenSettings] = useState(false);
  const [passwordModal, setPasswordModal] = useState(false);
  const [checked, setChecked] = useState(true);
  const [test, setTest] = useState(false);

  const dispatch = useDispatch();
  const infoUser = useSelector((state) => state.user.data);
  console.log(infoUser);

  const redirect = (e, route) => {
    e.stopPropagation();
    history.push(route);
    setChecked(!checked);
  };

  console.log('HISTORY', history);

  const { t, i18n } = useTranslation();

  const changeLanguage = (language) => {
    i18n.changeLanguage(language);
  };

  // const setPermissionsCallBack = () => {
  // const permissions = {};
  // if (infoUser?.status) {
  //   infoUser.permissions?.forEach((value) => {
  //     permissions[value.code] = value.action;
  //   });
  // }
  // return permissions;
  // };

  // eslint-disable-next-line consistent-return
  const formatDate = (isFullDate) => {
    if (infoUser?.last_signin_date) {
      return isFullDate
        ? format(parseISO(infoUser.last_signin_date), 'dd/MM/yyyy')
        : format(parseISO(infoUser.last_signin_date), 'HH:mm');
    }
  };

  // useEffect(() => {
  //   setUserPermissions(setPermissionsCallBack());
  // }, [infoUser]);

  const handleSettingsMenu = () => {
    setOpenSettings(!openSettings);
  };

  const handleSignOut = () => {
    dispatch(signOut());
  };

  const handlePasswordModal = () => {
    setPasswordModal(!passwordModal);
  };

  const handleMobileMenu = () => {
    setOpenMobile(!openMobile);
  };

  return (
    <>
      <Header mobileMenuIsOpen={openMobile} userSettingsIsOpen={openSettings}>
        <MobileMenu menuActive={checked}>
          <IconHamburgerMenu
            menuActive={checked}
            onClick={() => setChecked(!checked)}
          />
          <IconBackMenu
            menuActive={checked}
            onClick={() => setChecked(!checked)}
          />
        </MobileMenu>
        <UserContainer container>
          <UserSettings container justify="flex-end">
            <UserInfo onClick={() => handleSettingsMenu()}>
              <IconSingleUser />
              <Grid item>{infoUser?.name}</Grid>
            </UserInfo>
            <Fade in={openSettings}>
              <Settings>
                <SettingsName>{infoUser?.name}</SettingsName>
                {infoUser?.last_signin_date ? (
                  <LastLogin>
                    <LastLoginText>
                      {t('userSettings.lastAccess')}{' '}
                      {formatDate({ isFullDate: true })}
                    </LastLoginText>
                    <LastLoginText>{formatDate()}</LastLoginText>
                  </LastLogin>
                ) : (
                  <LastLogin>
                    <LastLoginText>
                      {t('userSettings.firstAccess')}
                    </LastLoginText>
                  </LastLogin>
                )}
                <SettingsOptions margin onClick={handlePasswordModal}>
                  <IconBiLock />
                  {t('userSettings.changePassword')}
                </SettingsOptions>
                <ModalChangePassword
                  open={passwordModal}
                  handlePasswordModal={handlePasswordModal}
                />
                <SettingsOptions onClick={handleSignOut}>
                  <IconBiExit />
                  {t('userSettings.signOut')}
                </SettingsOptions>
                <SettingsOptions margin unclickable>
                  {t('userSettings.selectLanguage')}
                </SettingsOptions>
                <SettingsOptions onClick={() => changeLanguage('pt-BR')}>
                  <CountryFlag src={BrazilLogo} />
                  {t('userSettings.langPTBR')}
                </SettingsOptions>
                <SettingsOptions onClick={() => changeLanguage('en-US')}>
                  <CountryFlag src={EuaLogo} />
                  {t('userSettings.langEnUS')}
                </SettingsOptions>
                {/* <SettingsOptions onClick={() => changeLanguage('es-EN')}>
                  <CountryFlag src={SpainLogo} />
                  {t('userSettings.langESP')}
                </SettingsOptions> */}
              </Settings>
            </Fade>
          </UserSettings>
        </UserContainer>
      </Header>
      <Slide direction="right" in={checked} mountOnEnter unmountOnExit>
        <Menu menuOpened={checked}>
          <Image
            src={imagem}
            color=""
            imageStyle={{
              height: '',
            }}
            style={{
              marginTop: '5px',
              marginLeft: '25px',
              width: '80%',
              marginBottom: '13vh',
              paddingTop: '',
            }}
          />
          <OptionMenu onClick={() => setOption2(!option2)}>
            <Grid>
              <IconBiFace />
              <Text>{t('buttons.merchants')}</Text>
            </Grid>

            <Collapse in={option2}>
              <SubOptionMenu
                onClick={(e) => redirect(e, '/merchants')}
                active={window.location.pathname === '/merchants'}
              >
                <IconGraphic />
                <SubText>{t('routes.transactions')}</SubText>
              </SubOptionMenu>
            </Collapse>
          </OptionMenu>
          <PermissibleRender
            userPermissions={infoUser?.type === 1 || infoUser?.type === 2}
            requiredPermissions={infoUser?.type === 1 || infoUser?.type === 2}
            renderOtherwide={null}
          >
            <OptionMenu onClick={() => setOption1(!option1)}>
              <Grid>
                <IconUsers />
                <Text>{i18n.t('buttons.users')}</Text>
              </Grid>

              <Collapse in={option1}>
                <SubOptionMenu
                  onClick={(e) => redirect(e, '/master')}
                  active={window.location.pathname === '/master'}
                >
                  <IconMaster />
                  <SubText>{t('routes.master')}</SubText>
                </SubOptionMenu>
                <SubOptionMenu
                  onClick={(e) => redirect(e, '/users')}
                  active={window.location.pathname === '/users'}
                >
                  <IconMaster />
                  <SubText>{t('routes.merchant')}</SubText>
                </SubOptionMenu>
              </Collapse>
            </OptionMenu>
          </PermissibleRender>

          <OptionMenu onClick={() => setOption2(!option2)}>
            <Grid>
              <IconReport />
              <Text>{t('buttons.reports')}</Text>
            </Grid>

            <Collapse in={option2}>
              <SubOptionMenu
                onClick={(e) => redirect(e, '/reports')}
                active={window.location.pathname === '/reports'}
              >
                <IconGraphic />
                <SubText>{t('routes.transactions')}</SubText>
              </SubOptionMenu>
              <SubOptionMenu
                onClick={(e) => redirect(e, '/general')}
                active={window.location.pathname === '/general'}
              >
                <IconGraphic />
                <SubText>{t('routes.general')}</SubText>
              </SubOptionMenu>
              <SubOptionMenu
                onClick={(e) => redirect(e, '/search')}
                active={window.location.pathname === '/search'}
              >
                <IconGraphic />
                <SubText>{t('routes.search')}</SubText>
              </SubOptionMenu>
            </Collapse>
          </OptionMenu>
        </Menu>
      </Slide>
      <Content mobileMenuIsOpen={checked}>{children}</Content>
    </>
  );
};

export default NavigationComponent;
