export function getTransactions(token, params) {
  return {
    type: '@transactions/GET_TRANSACTION_REQUEST',
    payload: { token, params },
  };
}

export function getTransactionsSuccess(transactions) {
  return {
    type: '@transactions/GET_TRANSACTION_SUCCESS',
    payload: { transactions },
  };
}

export function getTransactionsFailure(error) {
  return {
    type: '@transactions/GET_TRANSACTION_FAILURE',
    payload: { error },
  };
}

export function getTransactionsCleaner() {
  return {
    type: '@transactions/GET_TRANSACTION_CLEANER',
  };
}
