import { all, takeLatest, put } from 'redux-saga/effects';
import api from '../../../services/api';

import { getMerchantsRequests, getMerchantsFailure } from './actions';

function* getMerchants({ payload }) {
  const { token } = payload;

  try {
    const response = yield api.get('dashboard/merchants', {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    yield put(getMerchantsRequests(response.data));
  } catch (error) {
    yield put(getMerchantsFailure(error));
  }
}

export default all([
  takeLatest('@merchants/GET_MERCHANTS_REQUEST', getMerchants),
]);
