import { all } from 'redux-saga/effects';

import auth from './auth/sagas';
import user from './user/sagas';
import getTransactions from './getTransactions/sagas';
import showTransaction from './transactionShow/sagas';
import getMasters from './getMasters/sagas';
import createMaster from './CreateMaster/sagas';
import getMerchants from './merchantGet/sagas';
import showMerchant from './merchantShow/sagas';
import updateMaster from './masterUpdate/sagas';
import masterDelete from './masterDelete/sagas';
import getUsers from './getUsers/sagas';
import createUser from './CreateUser/sagas';
import updateUser from './userUpdate/sagas';
import deleteUser from './userDelete/sagas';

export default function* rootSaga() {
  return yield all([
    auth,
    user,
    getTransactions,
    showTransaction,
    getMasters,
    createMaster,
    updateMaster,
    masterDelete,
    getMerchants,
    showMerchant,
    getUsers,
    createUser,
    updateUser,
    deleteUser,
  ]);
}
