export function createUsersRequest(token, body) {
  console.log('aloalo');
  return {
    type: '@users/CREATE_USERS_REQUEST',
    payload: { token, body },
  };
}

export function createUsersSuccess(response) {
  return {
    type: '@users/CREATE_USERS_SUCCESS',
    payload: { response },
  };
}

export function createUsersCleaner() {
  return {
    type: '@users/CREATE_USERS_CLEANER',
  };
}

export function createUsersFailure(error) {
  return {
    type: '@users/CREATE_USERS_FAILURE',
    payload: { error },
  };
}
