import { all, takeLatest, call, put } from 'redux-saga/effects';
import api from '../../../services/api';

import { getMastersSuccess, getMastersFailure } from './actions';

function* getMasters({ payload }) {
  const { token, params } = payload;

  try {
    const response = yield api.get('dashboard/master', {
      params,
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    yield put(getMastersSuccess(response.data));
  } catch (error) {
    yield put(getMastersFailure(error));
  }
}

export default all([takeLatest('@masters/GET_MASTERS_REQUEST', getMasters)]);
