import { all, takeLatest, call, put } from 'redux-saga/effects';
import { decode } from 'jsonwebtoken';
import api from '../../../services/api';

import { signInSuccess, signFailure, signOut } from './actions';

function* signIn({ payload }) {
  const { user, pwd } = payload;
  const tokenp = `${user}:${pwd}`;
  const encodedToken = Buffer.from(tokenp).toString('base64');
  const headers = {
    Authorization: `Basic ${encodedToken}`,
  };
  try {
    const response = yield call(
      api.post,
      'dashboard/auth/token',
      {},
      {
        headers,
        auth: payload,
      }
    );
    const { token } = response.data;

    const userData = yield api.get('dashboard/auth/token/validate', {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });

    yield put(signInSuccess(token, userData.data));
  } catch (error) {
    yield put(signFailure(error));
  }
}

function* setToken({ payload }) {
  if (!payload) return;
  const { token } = payload.auth;

  console.log('hello');

  if (token) {
    const { exp: tokenExpiration } = decode(token);
    if (tokenExpiration <= Math.floor(Date.now() / 1000)) {
      console.log('hello, quite');
      yield put(signOut());
    }
  }
}

export default all([
  takeLatest('@auth/SIGN_IN_REQUEST', signIn),
  takeLatest('persist/REHYDRATE', setToken),
]);
