export function getMasters(token, params) {
  return {
    type: '@masters/GET_MASTERS_REQUEST',
    payload: { token, params },
  };
}

export function getMastersSuccess(users) {
  return {
    type: '@masters/GET_MASTERS_SUCCESS',
    payload: { users },
  };
}

export function getMastersFailure(error) {
  return {
    type: '@masters/GET_MASTERS_FAILURE',
    payload: { error },
  };
}
