import { all, put, takeLatest } from '@redux-saga/core/effects';
import api from '../../../services/api';

import { deleteUserFailure, deleteUserSuccess } from './actions';

function* deleteUser({ payload }) {
  const { token, id } = payload;
  console.log('REDUCER ANTES DO TRY');
  try {
    yield api.delete(`dashboard/user/${id}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    console.log('REDUCER PASSOU DO TRY');
    yield put(deleteUserSuccess());
  } catch (error) {
    yield put(deleteUserFailure(error));
  }
}

export default all([
  takeLatest('@users/DELETE_USER_REQUEST', deleteUser),
]);
