import { all, takeLatest, call, put } from 'redux-saga/effects';
import api from '../../../services/api';

import { showMerchantSuccess, showMerchantFailure } from './actions';

function* showMerchant({ payload }) {
  const { token, id } = payload;

  try {
    const response = yield api.get(`dashboard/merchants/${id}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    yield put(showMerchantSuccess(response.data));
  } catch (error) {
    yield put(showMerchantFailure(error));
  }
}

export default all([
  takeLatest('@merchants/SHOW_MERCHANT_REQUEST', showMerchant),
]);
