export function showMerchant(token, id) {
  return {
    type: '@merchants/SHOW_MERCHANT_REQUEST',
    payload: { token, id },
  };
}

export function showMerchantSuccess(merchant) {
  return {
    type: '@merchants/SHOW_MERCHANT_SUCCESS',
    payload: { merchant },
  };
}

export function showMerchantFailure(error) {
  return {
    type: '@merchants/SHOW_MERCHANT_FAILURE',
    payload: { error },
  };
}
