import React, { useMemo, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { Grid } from '@material-ui/core';
import Button from '../../components/button';
import Input from '../../components/input';
import Modal from '../../components/modal';
import {
  updateUserClear,
  updateUserRequest,
} from '../../store/modules/userUpdate/actions';
import LoadingComponent from '../../components/loading';

const INITIAL_STATE = {
  username: null,
  password: null,
};

export const UpdateUserModal = ({ open, closeModal, currentUser }) => {
  const { t, i18n } = useTranslation();
  const [updatedUser, setUpdatedUser] = useState(INITIAL_STATE);
  const [confirmPassword, setConfirmPassowrd] = useState('');
  const [wrongPassword, setWrongPassword] = useState(false);

  const token = useSelector((state) => state.auth.token);
  const userUpdated = useSelector((state) => state.updateUser);

  const dispatch = useDispatch();

  const handleSubmit = (e) => {
    e.preventDefault();

    if (updatedUser.password) {
      confirmPassword !== updatedUser.password
        ? setWrongPassword(true)
        : dispatch(updateUserRequest(token, currentUser.id, updatedUser));
    } else {
      dispatch(
        updateUserRequest(token, currentUser.id, {
          username: updatedUser.username,
        })
      );
    }
  };

  useMemo(() => {
    userUpdated.success && closeModal();
    userUpdated.success && dispatch(updateUserClear());
  }, [userUpdated]);

  return (
    <Modal
      modalState={open}
      onClose={closeModal}
      onBackdropClick={closeModal}
      title={t('modal.update_user')}
    >
      {!userUpdated.loading ? (
        <form onSubmit={handleSubmit}>
          <Grid container justify="start" style={{ marginTop: '10px' }}>
            <Input
              required
              defaultValue={currentUser.username}
              label={t('modal.name')}
              onChange={(e) => {
                setUpdatedUser((state) => ({
                  ...state,
                  username: e.target.value,
                }));
              }}
            />
          </Grid>
          <Grid container justify="start" style={{ marginTop: '10px' }}>
            <Input
              isInvalid={wrongPassword}
              label={t('modal.password')}
              type="password"
              onChange={(e) => {
                setUpdatedUser((state) => ({
                  ...state,
                  password: e.target.value,
                }));
              }}
            />
          </Grid>
          <Grid container justify="start" style={{ marginTop: '10px' }}>
            <Input
              isInvalid={wrongPassword}
              label={t('modal.type_password_confirm')}
              type="password"
              onChange={(e) => {
                setConfirmPassowrd(e.target.value);
              }}
            />
          </Grid>
          <Grid container justify="center">
            {wrongPassword && (
              <p style={{ color: 'red' }}>{t('modal.passwords_not_match')}</p>
            )}
          </Grid>
          <Grid
            container
            justify="space-between"
            width="45%"
            style={{ marginTop: '20px', marginBottom: '10px' }}
          >
            <Button
              variant="danger"
              width="45%"
              label={t('modal.return')}
              onClick={closeModal}
            />
            <Button
              variant="primary"
              width="45%"
              label={t('modal.update')}
              type="submit"
            />
          </Grid>
        </form>
      ) : (
        <LoadingComponent />
      )}
    </Modal>
  );
};
