import React, { useState, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { Autocomplete } from '@material-ui/lab';
import {
  TextField,
  Switch,
  Grid,
  TableContainer,
  Table,
  Paper,
} from '@material-ui/core';
import { DateTimePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import moment from 'moment';
import MomentUtils from '@date-io/moment';
import { useSelector } from 'react-redux';
import {
  SearchIcon,
  Header,
  ButtonSearch,
  StyledTableHead,
  StyledTableRow,
  StyledTableCell,
  StyledReportCell,
  useStyles,
  SearchField,
  ReportHeaderField,
} from './styles';
import 'moment/locale/pt-br';
import { StyledTableBody } from '../../components/transactions/getTransaction/styles';
import { Input } from '../../components/input/styles';

moment.locale('pt-br');

const INITIAL_QUERY = {
  start_date: null,
  end_date: null,
  type: null,
  status: null,
};

export const HeaderInputs = ({
  getTransactions,
  dispatch,
  setQuery,
  query,
  token,
}) => {
  const [queryParams, setQueryParams] = useState(INITIAL_QUERY);
  const totalTransactions = useSelector((state) => state.getTransactions);

  const classes = useStyles();

  const handleSearchTransactions = (e) => {
    e.preventDefault();
    dispatch(getTransactions(token, query));
  };

  const handleDate = (e, dateId) => {
    const date = moment(e, moment.ISO_8601).format('YYYY-MM-DDTHH:mm:ss.SSS');
    console.log(date);
    date &&
      setQueryParams((state) => ({
        ...state,
        [dateId]: date,
      }));
    console.log(queryParams);
  };

  console.log(queryParams);

  useMemo(() => {
    setQuery((state) => ({
      ...state,
      start_date: queryParams.start_date && `${queryParams.start_date}Z`,
      end_date: queryParams.end_date && `${queryParams.end_date}Z`,
      type: queryParams.type,
      status: queryParams.status,
    }));
  }, [queryParams]);

  const { t, i18n } = useTranslation();
  return (
    <form onSubmit={(e) => handleSearchTransactions(e)}>
      <Header>
        <ReportHeaderField
          container
          direction="row"
          style={{
            height: '132px',
          }}
        >
          <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="customized table">
              <StyledTableBody>
                <StyledTableRow>
                  <StyledTableCell>
                    {t('table.paid_transactions_value')}
                  </StyledTableCell>
                  <StyledTableCell>
                    {t('table.canceled_transactions_value')}
                  </StyledTableCell>
                  <StyledTableCell>
                    {t('table.waiting_payment_transactions_value')}{' '}
                  </StyledTableCell>
                  <StyledTableCell>
                    {t('table.total_transactions_value')}{' '}
                  </StyledTableCell>
                </StyledTableRow>
              </StyledTableBody>
              <StyledTableBody>
                <StyledTableRow>
                  {totalTransactions.data ? (
                    <>
                      <StyledReportCell
                        style={{
                          color: '#5DDE5D',
                          fontWeight: 'bold',
                          fontSize: '20px',
                        }}
                      >
                        {new Intl.NumberFormat('pt-BR', {
                          style: 'currency',
                          currency: 'BRL',
                        }).format(
                          totalTransactions.data.total_transactions_value / 100
                        )}
                      </StyledReportCell>
                      <StyledReportCell
                        style={{
                          color: 'red',
                          fontWeight: 'bold',
                          fontSize: '20px',
                        }}
                      >
                        {new Intl.NumberFormat('pt-BR', {
                          style: 'currency',
                          currency: 'BRL',
                        }).format(
                          totalTransactions.data.canceled_transactions_value /
                            100
                        )}
                      </StyledReportCell>
                      <StyledReportCell
                        style={{
                          color: '#FAAA08',
                          fontWeight: 'bold',
                          fontSize: '20px',
                        }}
                      >
                        {new Intl.NumberFormat('pt-BR', {
                          style: 'currency',
                          currency: 'BRL',
                        }).format(
                          totalTransactions.data.paid_transactions_value / 100
                        )}
                      </StyledReportCell>
                      <StyledReportCell
                        style={{
                          color: '#ACB5AC',
                          fontWeight: 'bold',
                          fontSize: '20px',
                        }}
                      >
                        {new Intl.NumberFormat('pt-BR', {
                          style: 'currency',
                          currency: 'BRL',
                        }).format(
                          totalTransactions.data.total_transactions_value / 100
                        )}
                      </StyledReportCell>
                    </>
                  ) : (
                    <>
                      <StyledReportCell>-</StyledReportCell>
                      <StyledReportCell>-</StyledReportCell>
                      <StyledReportCell>-</StyledReportCell>
                    </>
                  )}
                </StyledTableRow>
              </StyledTableBody>
            </Table>
          </TableContainer>
        </ReportHeaderField>

        <SearchField>
          <Grid
            container
            direction="column"
            style={{
              width: '45%',
              alignSelf: 'flex-start',
              justifySelf: 'flex-start',
              marginRight: '10px',
            }}
          >
            <Grid
              item
              direction="row"
              style={{
                alignSelf: 'flex-start',
                justifySelf: 'flex-start',
                marginBottom: '10px',
                width: '100%',
              }}
            >
              <MuiPickersUtilsProvider
                utils={MomentUtils}
                libInstance={moment}
                locale="pt-br"
              >
                <DateTimePicker
                  ampm={false}
                  label={t('table.initial_date')}
                  inputVariant="outlined"
                  value={queryParams.start_date}
                  format="DD/MM/YYYY HH:mm"
                  onChange={(e) => handleDate(e, 'start_date')}
                  style={{
                    alignSelf: 'flex-start',
                    justifySelf: 'flex-start',
                    marginBottom: '10px',
                    width: '100%',
                  }}
                />
              </MuiPickersUtilsProvider>
            </Grid>
            <Grid
              item
              direction="row"
              style={{
                alignSelf: 'flex-start',
                justifySelf: 'flex-start',
                marginBottom: '10px',
                width: '100%',
              }}
            >
              <MuiPickersUtilsProvider
                utils={MomentUtils}
                libInstance={moment}
                locale="pt-br"
              >
                <DateTimePicker
                  ampm={false}
                  label={t('table.final_date')}
                  showTodayButton
                  inputVariant="outlined"
                  value={queryParams.end_date}
                  format="DD/MM/YYYY HH:mm"
                  onChange={(e) => handleDate(e, 'end_date')}
                  style={{
                    alignSelf: 'flex-start',
                    justifySelf: 'flex-start',
                    marginBottom: '10px',
                    width: '100%',
                  }}
                />
              </MuiPickersUtilsProvider>
            </Grid>
          </Grid>
          <Grid
            container
            direction="row"
            style={{
              width: '45%',
              alignSelf: 'flex-start',
              justifySelf: 'flex-start',
            }}
          >
            <Autocomplete
              loadingText="Carregando..."
              options={['PIX', 'BANKSLIP', 'CARD', 'POSCARD']}
              getOptionLabel={(option) => t(`table.${option.toLowerCase()}`)}
              style={{
                width: '100%',
                marginBottom: '20px',
                marginRight: '10px',
              }}
              onChange={(e, target) => {
                // eslint-disable-next-line radix

                setQueryParams((state) => ({
                  ...state,
                  // eslint-disable-next-line no-unneeded-ternary
                  type: target ? target : null,
                }));
              }}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label={t('table.transaction_type')}
                  variant="outlined"
                />
              )}
            />

            <Autocomplete
              loadingText="Carregando..."
              options={[
                'AUTHORIZED',
                'DECLINED',
                'CANCELED',
                'WAITING',
                'PAID',
              ]}
              getOptionLabel={(option) => t(`table.${option.toLowerCase()}`)}
              style={{
                width: '100%',
                marginBottom: '10px',
                marginRight: '10px',
              }}
              onChange={(e, target) => {
                // eslint-disable-next-line radix
                target &&
                  setQueryParams((state) => ({
                    ...state,
                    // eslint-disable-next-line no-unneeded-ternary
                    status: target ? target : null,
                  }));
              }}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label={t('table.status')}
                  variant="outlined"
                />
              )}
            />
          </Grid>
          <Grid
            container
            direction="row"
            style={{
              width: '45%',
              alignSelf: 'flex-start',
              justifySelf: 'flex-start',
            }}
          >
            <Input
              placeholder="Serial POS"
              style={{
                width: '100%',
                height: '55px',
                marginBottom: '20px',
                fontSize: '19px',
              }}
              onChange={(e) =>
                setQueryParams((state) => ({
                  ...state,
                  serial_pos: e.target.value ? e.target.value : null,
                }))
              }
            />
            <ButtonSearch type="submit">
              <SearchIcon />
            </ButtonSearch>
          </Grid>
        </SearchField>
      </Header>
    </form>
  );
};
