/* eslint-disable prefer-template */
import { Grid } from '@material-ui/core';
import React, { useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useDispatch, useSelector } from 'react-redux';
import Modal from '../../components/modal';
import Button from '../../components/button';
import LoadingComponent from '../../components/loading';
import { deleteMaster, deleteMasterClear } from '../../store/modules/masterDelete/actions';

export const DeleteUserModal = ({ open, closeModal, currentUser }) => {
  const { t, i18n } = useTranslation();
  const token = useSelector((state) => state.auth.token);
  const userDeleted = useSelector((state) => state.deleteMaster);

  const dispatch = useDispatch();

  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(deleteMaster(token, currentUser.id));
  };

  useMemo(() => {
    userDeleted.success && closeModal();
    userDeleted.success && dispatch((deleteMasterClear()));
  }, [userDeleted]);

  useMemo(() => {
    dispatch(deleteMasterClear());
  }, [open]);

  return (
    <Modal
      modalState={open}
      onClose={closeModal}
      onBackdropClick={closeModal}
      title={(t('modal.delete_user') + '"' + currentUser.name + '"?')}
    >
      {
        userDeleted.error && (
          <>
            <Grid
              container
              justify="space-between"
              style={{ marginBottom: '30px' }}
            >
              <p>{`${userDeleted.error}`}</p>
            </Grid>
          </>
        )
      }
      {!userDeleted.loading ? (
        <Grid
          container
          justify="space-between"
          style={{ marginTop: '20px', marginBottom: '30px' }}
        >
          <Button variant="danger" width="45%" label={t('modal.return')} onClick={closeModal} />
          <Button
            variant="primary"
            width="45%"
            label={t('modal.delete')}
            type="submit"
            onClick={handleSubmit}
          />
        </Grid>
      ) : (
        <LoadingComponent />
      )}
    </Modal>
  );
};
