import { all, takeLatest, call, put } from 'redux-saga/effects';
import api from '../../../services/api';
import { createUsersSuccess, createUsersFailure } from './actions';

function* createUsers({ payload }) {
  console.log('passando aqui');
  const { token, body } = payload;
  try {
    const response = yield api.post('dashboard/user', body, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    console.log(response);
    yield put(createUsersSuccess(response.data));
  } catch (error) {
    yield put(createUsersFailure(error));
  }
}

export default all([takeLatest('@users/CREATE_USERS_REQUEST', createUsers)]);
