export function updateMaster(token, id, body) {
  console.log('UPDATE MASTER ACTION REQUEST');
  return {
    type: '@masters/UPDATE_MASTER_REQUEST',
    payload: { token, id, body },
  };
}

export function updateMasterSuccess(response) {
  return {
    type: '@masters/UPDATE_MASTER_SUCCESS',
    payload: { response },
  };
}

export function updateMasterFailure(error) {
  return {
    type: '@masters/UPDATE_MASTER_FAILURE',
    payload: { error },
  };
}

export function updateMasterClear() {
  return {
    type: '@masters/UPDATE_MASTER_CLEAR',
  };
}
