import { all, takeLatest, call, put } from 'redux-saga/effects';
import api from '../../../services/api';

import { createMasterFailure, createMasterSuccess } from './actions';

function* createMaster({ payload }) {
  const { token, body } = payload;
  try {
    const response = yield api.post('dashboard/master', body, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    yield put(createMasterSuccess(response.data));
  } catch (error) {
    yield put(createMasterFailure(error));
  }
}

export default all([
  takeLatest('@masters/CREATE_MASTER_REQUEST', createMaster),
]);
