import { combineReducers } from 'redux';
import auth from './auth/reducer';
import user from './user/reducer';
import getTransactions from './getTransactions/reducer';
import getMasters from './getMasters/reducer';
import showTransaction from './transactionShow/reducer';
import createMaster from './CreateMaster/reducer';
import getMerchants from './merchantGet/reducer';
import showMerchant from './merchantShow/reducer';
import updateMaster from './masterUpdate/reducer';
import deleteMaster from './masterDelete/reducer';
import getUsers from './getUsers/reducer';
import createUser from './CreateUser/reducer';
import updateUser from './userUpdate/reducer';
import deleteUser from './userDelete/reducer';

export default combineReducers({
  auth,
  user,
  getTransactions,
  showTransaction,
  getMasters,
  createMaster,
  updateMaster,
  deleteMaster,
  getMerchants,
  showMerchant,
  getUsers,
  createUser,
  updateUser,
  deleteUser,
});
